  .termsCheck{
    display: block;
    position: relative;
    /* padding-left: 45px; */
    margin: auto;
    cursor: pointer;
    font-size: 20px;
  }

  input[type=checkbox] {
    visibility: hidden;
}
.termsStyle {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: white;
  border-radius: 7px;
  border-color:rgba(103,103,103,1);
  border-style: solid;
  border-radius: 7px;
  border-width:1px;
}
/* .termsCheck:hover input ~ .termsStyle {
  background-color: #D11F7B;
  opacity: .5;
} */
.termsCheck input:checked ~ .termsStyle {
  background-color: #D11F7B;
  opacity: 1;
}
.termsCheck input:checked ~ .termsStyle:after {
  display: block;
  background-color: #D11F7B;
}
.termsCheck input:active ~ .termsStyle {
  background-color: #D11F7B;
  opacity: 1;
}
.termsStyle:after {
  content: "";
  position: absolute;
  display: none;
  
}
.termsCheck .termsStyle:after {
  left: 8px;
  bottom: 5px;
  width: 6px;
  height: 12px;
  border: solid rgb(0, 0, 0);
  border-width: 0 4px 4px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
